<template>
  <div id="app">
    <div class="page-header">
      <div class="left">
        <a href="/vxe-table/">
          <img src="/vxe-table/logo.png" width="18">
          <span class="title">vxe-table</span>
        </a>
        <a href='https://gitee.com/xuliangzhan_admin/vxe-table/stargazers'>
          <img src='https://gitee.com/xuliangzhan_admin/vxe-table/badge/star.svg?theme=gvp' alt='star'>
        </a>
        <a href="https://github.com/x-extends/vxe-table/stargazers">
          <img src="https://img.shields.io/github/stars/xuliangzhan/vxe-table.svg">
        </a>
        <a href="http://npm-stat.com/charts.html?package=vxe-table">
          <img src="https://img.shields.io/npm/dm/vxe-table.svg">
        </a>
      </div>
      <div class="right">
        <span v-if="loginToken && loginInfo">
          <vxe-button type="text" status="success" class="login2-btn" :content="loginInfo.no">
            <template v-slot:dropdowns>
              <!-- <vxe-button type="text" content="修改登录密码"></vxe-button> -->
              <!-- <vxe-button type="text" content="修改授权密码" @click="updateDCPawdEvent"></vxe-button> -->
              <vxe-button type="text" content="退出登录" @click="logoutEvent"></vxe-button>
            </template>
          </vxe-button>
        </span>
        <span v-else>
          <span class="get-contact">购买授权与定制化插件，请先联系邮件咨询：<a class="link" href="mailto:xu_liangzhan@163.com">xu_liangzhan@163.com</a></span>
          <vxe-button id="login" size="mini" class="login-btn" @click="loginEvent" :disabled="loading">登录</vxe-button>
        </span>
      </div>
    </div>
    <div class="page-nav">
      <!-- <router-link class="link" :to="{name: 'Support'}">技术支持群</router-link> | -->
      <router-link class="link" :to="{name: 'Pro'}">PRO扩展</router-link> |
      <router-link class="link" :to="{name: 'ProAuth'}">PRO 授权查询</router-link> |
      <router-link class="link" :to="{name: 'Filter'}">筛选扩展</router-link> |
      <!-- <router-link class="link" :to="{name: 'Edit'}">编辑扩展</router-link> -->
    </div>
    <div class="page-body">
      <router-view/>
    </div>
    <vxe-modal v-model="lnVisible" :loading="lnLoading" title="登录授权" destroy-on-close>
      <template v-slot>
        <vxe-form :data="lnFormData" :rules="lnFormRules" @submit="submitLoginEvent">
          <vxe-form-item field="name" title="用户名" span="24" title-width="60" title-align="right" :item-render="{name: '$input', props: {placeholder: '请输入用户名'}}"></vxe-form-item>
          <vxe-form-item field="pawd" title="密码" span="24" title-width="60" title-align="right" :item-render="{name: '$input', props: {placeholder: '请输入密码'}}"></vxe-form-item>
          <vxe-form-item align="center" span="24">
            <template v-slot>
              <vxe-button @click="lnVisible = false">取消</vxe-button>
              <vxe-button status="primary" type="submit">登录</vxe-button>
            </template>
          </vxe-form-item>
        </vxe-form>
      </template>
    </vxe-modal>

    <vxe-modal v-model="dcVisible" :loading="dcLoading" title="修改授权密码" destroy-on-close>
      <template v-slot>
        <vxe-form :data="dcFormData" :rules="dcFormRules" @submit="submitDCPawdEvent">
          <vxe-form-item field="oldPawd" title="旧的密码" span="24" :item-render="{name: '$input', props: {placeholder: '请输入旧的密码'}}"></vxe-form-item>
          <vxe-form-item field="newPawd" title="新的密码" span="24" :item-render="{name: '$input', props: {placeholder: '请输入新的密码'}}"></vxe-form-item>
          <vxe-form-item field="confirmPawd" title="确认密码" span="24" :item-render="{name: '$input', props: {placeholder: '请输入确认密码'}}"></vxe-form-item>
          <vxe-form-item align="center" span="24">
            <template v-slot>
              <vxe-button @click="dcVisible = false">取消</vxe-button>
              <vxe-button status="primary" type="submit">修改</vxe-button>
            </template>
          </vxe-form-item>
        </vxe-form>
      </template>
    </vxe-modal>
  </div>
</template>

<script>
import XEAjax from 'xe-ajax'
import XEUtils from 'xe-utils'
import { mapState, mapMutations } from 'vuex'
import store from './store'

if (process.env.NODE_ENV === 'development') {
  window.XEAjax = XEAjax
}

const winPrompt = prompt
const winAlert = alert

XEAjax.setup({
  origin: process.env.NODE_ENV === 'development' ? 'http://localhost:8888' : 'https://api.xuliangzhan.com:10443'
})

XEAjax.interceptors.request.use((request, next) => {
  const loginToken = store.state.loginToken
  if (loginToken) {
    request.headers.set('token', loginToken)
  }
  if (request.url.indexOf('/api/pub/vxetable/') > -1) {
    request.headers.set('path', location.href)
    request.headers.set('name', 'vxetable')
    request.headers.set('now', Date.now())
  }
  next()
})

export default {
  data () {
    return {
      loading: false,
      invalidCode: [],

      lnVisible: false,
      lnLoading: false,
      lnFormData: {
        name: '',
        pawd: ''
      },
      lnFormRules: {
        name: [
          {
            validator: ({ itemValue }) => {
              if (!itemValue) {
                return new Error('请输入用户名')
              }
            }
          }
        ]
      },

      dcVisible: false,
      dcLoading: false,
      dcFormData: {
        confirmPawd: '',
        newPawd: '',
        oldPawd: ''
      },
      dcFormRules: {
        confirmPawd: [
          { required: true, message: '请再次输入密码' },
          {
            validator: ({ data }) => {
              if (data.newPawd !== data.confirmPawd) {
                return new Error('两次输入的密码不一致')
              }
            }
          }
        ],
        newPawd: [
          { required: true, message: '请输入新密码' },
          { pattern: /^[a-zA-Z0-9@!^$~+#-]{8,200}$/, message: '密码至少8位数，由数字或字母组成' }
        ],
        oldPawd: [
          { required: true, message: '请输入旧密码' }
        ]
      }
    }
  },
  computed: {
    ...mapState([
      'loginStatus',
      'loginToken',
      'loginInfo'
    ])
  },
  watch: {
    loginStatus () {
      this.vaildLogin()
    }
  },
  created () {
    XEAjax.get('/api/pub/vxetable/apps').then(data => {
      this.setAppList(this.$utils.getRest(data))
    })
    this.vaildLogin()
  },
  methods: {
    ...mapMutations([
      'setLoginToken',
      'setLoginInfo',
      'setAppList'
    ]),
    payEvent () {
      this.$router.push({ name: 'Pay' }).catch(e => e)
    },
    vaildLogin () {
      if (this.loginToken) {
        const strs = 'ijkoefgcdlmnphzytwab'.split('')
        const params = {}
        XEUtils.sample(strs, 2).forEach(key => {
          params[key] = XEUtils.random(15, 999)
        })
        XEAjax.get(`/api/pub/vxetable/vaild/${this.loginToken}`, params).then(data => {
          const rest = this.$utils.getRest(data)
          if (rest) {
            if (rest.code === 0 && XEUtils.isArray(rest.result.compList)) {
              this.setLoginInfo(rest.result)
            } else {
              this.logoutEvent()
            }
          }
        }).catch((data) => {
          const rest = this.$utils.getRest(data)
          if (rest && rest.code) {
            this.logoutEvent()
          }
        })
      }
    },
    logoutEvent () {
      this.setLoginToken(null)
      this.setLoginInfo(null)
      sessionStorage.removeItem('AUTH_TOKEN')
    },
    submitLoginEvent () {
      this.lnVisible = true
    },
    loginEvent () {
      let code = winPrompt(decodeURIComponent('%E8%AF%B7%E8%BE%93%E5%85%A5%E7%99%BB%E5%BD%95%E7%9A%84%E7%94%A8%E6%88%B7%EF%BC%9A'))
      if (code) {
        code = code.trim()
      }
      if (code) {
        if (!this.invalidCode.includes(code)) {
          const strs = 'zytwabphijklmncdoefg'.split('')
          const params = {}
          XEUtils.sample(strs, 4).forEach(key => {
            params[key] = XEUtils.random(15, 999)
          })
          this.loading = true
          XEAjax.post(`/api/pub/vxetable/login/${encodeURIComponent(code)}`, {
            n: Date.now(),
            c: code
          }, { params }).then(data => {
            const rest = this.$utils.getRest(data)
            this.loading = false
            if (rest && rest.result && rest.result.token) {
              this.setLoginToken(rest.result.token)
              this.setLoginInfo(Object.assign({}, rest.result, { token: undefined }))
              sessionStorage.setItem('AUTH_TOKEN', rest.result.token)
            } else {
              this.logoutEvent()
              winAlert(decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'))
            }
          }).catch((data) => {
            if (data) {
              const rest = this.$utils.getRest(data)
              this.loading = false
              this.invalidCode.push(code)
              if (rest.code) {
                this.logoutEvent()
              }
              winAlert(rest.message)
            } else {
              winAlert(decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'))
            }
          })
        } else {
          winAlert(decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'))
        }
      }
    },
    updateDCPawdEvent () {
      this.dcVisible = true
    },
    submitDCPawdEvent () {
      this.dcLoading = true
      XEAjax.post('/api/pub/vxetable/dcpawd/update', this.dcFormData).then(data => {
        const rest = this.$utils.getRest(data)
        this.dcLoading = false
        if (rest && rest.result && rest.result.update === 'success') {
          this.dcVisible = false
          XEUtils.clear(this.dcFormData, '')
          this.$XModal.message({
            message: rest.message,
            title: '操作成功',
            status: 'success'
          })
        } else {
          this.$XModal.message({
            message: rest.message,
            title: '操作失败',
            status: 'error'
          })
        }
      }).catch(() => {
        this.dcLoading = false
        this.$XModal.message({
          message: decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'),
          title: '操作失败',
          status: 'error'
        })
      })
    }
  }
}
</script>

<style lang="scss">
*, :after, :before {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}
html, body {
  height: 100%;
  overflow: hidden;
}
body {
  margin: 0;
  padding: 0;
  color: #606266;
  background-color: #F7F8FA;
  font-size: 14px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB', 'Microsoft YaHei', 'Helvetica Neue', Helvetica, Arial, sans-serif, 'Apple Color Emoji', 'Segoe UI Emoji', 'Segoe UI Symbol';
  font-variant: tabular-nums;
}

img {
  vertical-align: middle;
  border-style: none;
}
.link {
  color: #409eff;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }
}
#app {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.pay-btn {
  display: inline-block;
  color: #fff;
  margin: 0 0 0 10px;
  outline: 0;
  border: 0;
  padding: 0.2em 0.8em;
  border-radius: 4px;
  background-color: #fb7d27;
  user-select: none;
  &[disabled] {
    cursor: no-drop;
  }
  &:not([disabled]) {
    cursor: pointer;
    &:hover {
      background-color: #fb9047;
    }
    &:active {
      background-color: #ff6700;
    }
  }
}
.page-header {
  display: flex;
  height: 48px;
  flex-shrink: 0;
  align-items: center;
  color: #fff;
  padding: 0 20px;
  background-color: #24292e;
  border-bottom: 1px solid #dcdfe6;
  box-shadow: -2px 2px 6px rgba(0, 0, 0, 0.12);
  & > .left {
    height: 100%;
    padding: 5px 0;
    font-size: 22px;
    flex-shrink: 0;
    a {
      text-decoration: none;
    }
  }
  & > .right {
    flex-grow: 1;
    text-align: right;
  }
  .get-contact {
    color: #f6ca9d;
  }
  .title {
    padding: 0 0.4em;
    font-weight: 700;
    color: #fff;
  }
  .link {
    color: #fff;
  }
  .login-btn {
    margin: 0 0 0 10px;
  }
}
.page-nav {
  padding: 20px;
  text-align: center;
  font-size: 16px;
  flex-shrink: 0;
  user-select: none;
  a {
    font-weight: bold;
    color: #2c3e50;

    &.router-link-exact-active {
      color: #409eff;
    }
  }
}
.page-body {
  overflow: auto;
  flex-grow: 1;
}
.update-log {
  font-weight: 700;
  font-size: 16px;
  margin-left: 15px;
  text-decoration: none;
  color: #42b983;
  cursor: pointer;
  &:hover {
    text-decoration: underline;
  }
}
.download-lib {
  display: inline-block;
  color: #fff;
  border-color: #409eff;
  background-color: #409eff;
  border-radius: 4px;
  padding: 2px 8px;
  margin: 0 5px;
  font-size: 12px;
  cursor: pointer;
}
</style>
