<template>
  <div v-if="selectComp" class="page-view">
    <div class="comp-tip">
      <div>
        <span class="comp-name">{{ selectComp.name }}</span>
        <template v-if="authComp">
          <span class="comp-type"> - 已授权{{ $utils.getCompType(authComp.authType) }}</span>
          <span class="comp-support">
            <span>技术支持有效期：</span>
            <span v-if="loginInfo.currDate > authComp.supportExpiryDate" class="content red">已过期</span>
            <span v-else class="content green">{{ authComp.supportExpiryDate }}</span>
          </span>
        </template>
        <template v-else>
          <span class="comp-type"> - {{ $utils.getCompType(selectComp.codeType) }}</span>
          <span class="comp-amoun">￥{{ selectComp.amount }}</span>
          <span class="comp-expire">/ {{ $utils.getCompExpiration(selectComp.deadline) }}</span>
          <span class="comp-support" v-if="selectComp.supportDay >= 365">提供 1 年技术支持</span>
        </template>
      </div>
      <ul>
        <li>{{ selectComp.codeType === 'src' ? '该授权包含完整源码' : '该授权不提供源码'}}</li>
      </ul>
      <div class="comp-agreement">
        <a class="link" @click="$refs.authAgreement.open()">查看详细协议</a>
      </div>
      <div class="comp-oper">
        <vxe-button class="btn" status="danger" @click="$refs.compOrder.open()" v-if="!authComp">购买授权</vxe-button>
        <vxe-button class="btn" status="primary" @click="$refs.releaseLog.open()">{{ authComp ? 'v3 版本' : 'v3更新日志' }}</vxe-button>
        <vxe-button class="btn" status="primary" @click="$refs.releaseNextLog.open()">{{ authComp ? 'v4 版本' : 'v4更新日志' }}</vxe-button>
        <vxe-button class="btn" @click="$router.replace({name: backName})">返回</vxe-button>
      </div>
    </div>
    <div class="comp-preview">
      <div class="preview">
        <img :src="imgUrl">
      </div>
      <ul class="features">
        <li v-for="(item, index) in selectComp.features" :key="index">{{ item }}</li>
      </ul>
    </div>
    <div class="comp-api">
      <a class="link" href="/vxe-table/#/table/api?q=pro" target="_black">查看更多 API</a>
    </div>
    <div class="comp-desc">
      <pre>
        <pre-code class="text">{{ apiDesc[0] }}</pre-code>
      </pre>
    </div>
    <pre>
      <pre-code class="html">{{ demoCodes[0] }}</pre-code>
      <pre-code class="javascript">{{ demoCodes[1] }}</pre-code>
    </pre>
    <release-log ref="releaseLog" :code="code"></release-log>
    <release-log ref="releaseNextLog" :code="code" :releaseCode="`${code}Next`"></release-log>
    <comp-order ref="compOrder" :code="code"></comp-order>
    <auth-agreement ref="authAgreement" :code="code"></auth-agreement>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {
      code: 'PRO',
      backName: 'Pro',
      imgUrl: require('../../assets/image/plugin/proClip.gif'),
      apiDesc: [
        `
        安装：
        将 plugins/table/pro 复制到项目任意目录中，引用 vxe-table-pro.es6.min.js 和 vxe-table-pro.min.css 即可

        鼠标配置
        mouse-config：{
          area 是否开启鼠标单元格区域选取
          extension 是否开启鼠标点击区域内右下角延伸按钮
        }

        复制粘贴配置项
        clip-config: {
          isCopy 是否启用复制功能
          isCut 是否启用剪贴功能
          isPaste 是否启用粘贴功能
          isRowIncrement 是否启用行自增，当粘贴的行数超出表格时自动插入新行
          isColumnIncrement 是否启用列自增，当粘贴的列数超出表格时自动插入新列
        }

        按键配置
        keyboard-config：{
          isClip 是否开启（Ctrl+X键、Ctrl+C键、Ctrl+V键）复制粘贴功能
          isMerge 是否开开启（Ctrl+M键）合并和取消合并功能
          isFNR 是否开启（Ctrl+F键、Ctrl+H键）查找和替换功能
          isEdit 是否开启（Esc键、F2键、任意键）单元格编辑功能
          isTab 是否开启（Tab键、Shift+Tab键）单元格左右移动功能
          isArrow 是否开启（方向键）单元格上下左右移动功能
          isEnter 是否开启（Enter键、Shift+Enter键）单元格上下移动功能
          isDel 是否开启（Delete键、Backspace键）清空单元格的值
          isChecked 是否开启（Spacebar键）切换复选框和单选框状态
          enterToTab 是否将回车键行为改成 Tab 键行为
        }
        `
      ],
      demoCodes: [
        `
        <vxe-table
          border
          show-overflow
          height="500"
          :mouse-config="{area: true}"
          :data="tableData">
          <vxe-table-column type="seq" width="60"></vxe-table-column>
          <vxe-table-column field="a" title="A1"></vxe-table-column>
          <vxe-table-column field="b" title="B1"></vxe-table-column>
          <vxe-table-column field="c" title="C1"></vxe-table-column>
          <vxe-table-column field="d" title="D1"></vxe-table-column>
        </vxe-table>
        `,
        `
        export default {
          data () {
            return {
              tableData: [
                { id: 10001, a: '', b: '', c: '', d: '', e: '', f: '', g: '' },
                { id: 10002, a: '', b: '', c: '', d: '', e: '', f: '', g: '' },
                { id: 10003, a: '', b: '', c: '', d: '', e: '', f: '', g: '' },
                { id: 10004, a: '', b: '', c: '', d: '', e: '', f: '', g: '' }
              ]
            }
          }
        }
        `
      ]
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList'
    ]),
    authComp () {
      if (this.loginToken && this.loginInfo && this.loginInfo.compList) {
        return this.loginInfo.compList.find(comp => comp.appCode === this.code)
      }
      return null
    },
    selectComp () {
      return this.appList.find(item => item.code === this.code)
    }
  }
}
</script>
