<template>
  <div v-if="selectComp" class="order-page">
    <vxe-modal v-model="emailVisible" title="验证邮箱" height="200" width="440" destroy-on-close>
      <vxe-form ref="xForm" :data="formData" :rules="formRules" @submit="vaildEmail" title-width="70">
        <vxe-form-item field="email" title="邮箱">
          <template v-slot="scope">
            <vxe-input v-model="formData.email" placeholder="请输入邮箱" clearable @input="$refs.xForm.updateStatus(scope)"></vxe-input>
            <vxe-button @click="getEmail">{{ checkEmail ? '获取验证码' : '重新获取' }}</vxe-button>
          </template>
        </vxe-form-item>
        <vxe-form-item field="code" title="验证码">
          <template v-slot="scope">
            <vxe-input v-model="formData.code" placeholder="请输入验证码" clearable @input="$refs.xForm.updateStatus(scope)"></vxe-input>
            <vxe-button status="primary" type="submit" :disabled="!formData.email">下一步</vxe-button>
          </template>
        </vxe-form-item>
      </vxe-form>
    </vxe-modal>
    <vxe-modal v-model="visible" :loading="loading" :show-header="false" width="460" height="440">
      <div class="pay-popup" v-if="selectPay">
        <div class="pay-desc">
          <img class="pay-icon" src="../assets/image/support/alipay_icon.png">
          <span>支付宝扫码付款<span class="invoice-desc">（不支持发票）</span></span>
        </div>
        <div class="pay-qr">
          <div class="pay-left">
            <img :src="qrUrl">
          </div>
          <div class="pay-right">
            <div class="pay-price">
              <span class="price-unit">￥{{ selectPay.amount }}</span>
            </div>
            <div class="pay-down">
              <div class="pay-msg">请在有效时间内完成付款<br>超时将自动取消</div>
              <div class="pay-time">{{ payCountdown }}</div>
            </div>
            <div class="pay-oper">
              <vxe-button status="info" @click="cancelPay" size="mini">取消付款</vxe-button>
            </div>
          </div>
        </div>
        <div class="pay-name">产品名称：{{ selectPay.appCode }}</div>
        <div class="pay-no">订单单号：{{ selectPay.orderNo }}</div>
        <div class="pay-type">支付方式：支付宝</div>
        <div class="pay-name">邮箱账号：{{ selectPay.email || '未绑定' }}</div>
      </div>
    </vxe-modal>
    <vxe-modal v-model="proVisible" :loading="proLoading" title="PRO 购买授权" width="500" height="440" esc-closable>
      <div class="pro-desc">
        <span>请先联系客服QQ <a class="link" href="http://wpa.qq.com/msgrd?v=3&uin=405294094&site=qq&menu=yes" target="_black">405294094</a> 或邮件 <a href="mailto:xu_liangzhan@163.com" class="link">xu_liangzhan@163.com</a> 咨询</span>
      </div>
      <div class="pro-oper">
        <vxe-radio-group v-model="proType">
          <vxe-radio label="zfb">支付宝<span class="invoice-desc">（不支持发票）</span></vxe-radio>
          <vxe-radio label="wx">微信<span class="invoice-desc">（不支持发票）</span></vxe-radio>
        </vxe-radio-group>
      </div>
      <div class="pro-amoun" v-if="selectComp">
        <div class="comp-amoun">￥{{ selectComp.amount }}</div>
      </div>
      <div class="pro-qr" :style="{visibility: proLoading ? 'hidden' : 'visible'}">
        <img v-if="proType === 'zfb'" src="../assets/image/support/pro_zfb.png" height="200">
        <img v-else src="../assets/image/support/pro_wx.png" height="200">
      </div>
      <div class="pro-desc">
        <span class="red">（注：付款完成将交易订单号发给客服QQ <a class="link" href="http://wpa.qq.com/msgrd?v=3&uin=405294094&site=qq&menu=yes" target="_black">405294094</a> 即可）</span>
      </div>
    </vxe-modal>
  </div>
</template>

<script>
import { mapMutations, mapState } from 'vuex'
import XEAjax from 'xe-ajax'
import XEUtils from 'xe-utils'
import QRCode from 'qrcode'

const winAlert = alert

export default {
  name: 'CompOrder',
  props: {
    code: String
  },
  data () {
    return {
      emailVisible: false,
      checkEmail: true,
      formData: {
        email: '',
        code: ''
      },
      formRules: {
        email: [
          { required: true, message: '请输入有效的邮箱账号' }
        ],
        code: [
          { required: true, message: '请输入验证码' }
        ]
      },
      formItems: [
        { title: '邮箱', field: 'email' }
      ],

      proVisible: false,
      proLoading: false,
      proType: 'zfb',

      visible: false,
      loading: false,
      selectPay: null,
      qrUrl: '',
      payCountdown: ''
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList'
    ]),
    selectComp () {
      return this.appList.find(item => item.code === this.code)
    }
  },
  destroyed () {
    this.proVisible = false
    this.visible = false
  },
  methods: {
    ...mapMutations([
      'updateLoginInfo'
    ]),
    open () {
      XEUtils.clear(this.formData, '')
      this.updateLoginInfo()
      if (this.code === 'PRO') {
        this.proLoading = true
        this.proVisible = true
        setTimeout(() => {
          this.proLoading = false
        }, 300)
      } else {
        this.proVisible = false
        if (this.loginToken && this.loginInfo) {
          this.pay()
        } else {
          this.$XModal.confirm({
            escClosable: true,
            message: '当前未登录，是否登录！',
            title: '登录提示',
            cancelButtonText: '注册一个',
            confirmButtonText: '马上登录'
          }).then(type => {
            if (type === 'confirm') {
              document.getElementById('login').click()
            } else if (type === 'cancel') {
              this.emailVisible = true
            }
          })
        }
      }
    },
    getEmail () {
      this.checkEmail = true
      XEAjax.post('/api/pub/vxetable/check/email', {
        email: this.formData.email
      }).then(data => {
        const rest = this.$utils.getRest(data)
        if (rest && rest.result && rest.result.email) {
          this.checkEmail = false
          this.$XModal.message({
            message: '验证码发送成功',
            status: 'success'
          })
        } else {
          this.$XModal.message({
            message: rest.message,
            status: 'error'
          })
        }
      })
    },
    vaildEmail () {
      if (!this.formData.email) {
        this.$XModal.message({
          message: '邮箱不能为空',
          status: 'error'
        })
        return
      }
      if (!this.formData.code) {
        this.$XModal.message({
          message: '验证码不能为空',
          status: 'error'
        })
        return
      }
      XEAjax.post('/api/pub/vxetable/vaild/email', this.formData).then(data => {
        const rest = this.$utils.getRest(data)
        if (rest && rest.result && rest.result.email) {
          this.checkEmail = false
          this.pay(rest.result.email)
        } else {
          this.$XModal.message({
            message: rest.message,
            status: 'error'
          })
        }
      })
    },
    cancelPay () {
      this.$XModal.confirm({
        message: '是否取消付款？',
        title: '授权提示'
      }).then(type => {
        if (type === 'confirm') {
          this.visible = false
          this.updateLoginInfo()
        }
      })
    },
    pay (email) {
      this.emailVisible = false
      this.visible = true
      this.qrUrl = ''
      this.payCountdown = ''
      this.selectPay = null
      XEAjax.post(`/api/auth/pay/alipay/create/${this.code}`, {
        email
      }).then(data => {
        const rest = this.$utils.getRest(data)
        if (rest && rest.result) {
          this.loading = false
          this.selectPay = rest.result
          this.getAlipayStatus().then((rest) => {
            this.visible = false
            this.updateLoginInfo()
            if (rest && rest.status === 'success') {
              this.$XModal.alert({
                message: '授权成功，授权信息已发送到邮箱中，请注意查收！',
                title: '支付成功',
                status: 'warning'
              })
            } else if (rest && rest.status === 'cancel') {
            } else {
              this.$XModal.alert({
                message: '该订单支付超时，本次支付已取消！',
                title: '支付超时',
                status: 'warning'
              })
            }
          }).catch(() => {
            this.visible = false
            this.updateLoginInfo()
            this.$XModal.alert({
              message: '授权失败，若该订单已支付成功，则在7个工作日内退回',
              title: '支付失败',
              status: 'error'
            })
          })
          QRCode.toDataURL(rest.result.qrCode, { width: 200 }).then(url => {
            this.qrUrl = url
          })
        } else {
          winAlert(decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'))
        }
      }).catch(() => {
        winAlert(decodeURIComponent('%E6%97%A0%E6%9D%83%E9%99%90%E6%93%8D%E4%BD%9C'))
      })
    },
    getAlipayStatus () {
      let isOut = false
      const orderNo = this.selectPay.orderNo
      const timeout = XEUtils.toNumber(this.selectPay.timeout)
      const endDate = new Date(Date.now() + Math.max(1000, timeout - 1000))
      const outPromise = new Promise(resolve => {
        const updateDowntime = () => {
          if (this.visible) {
            const dateDiff = XEUtils.getDateDiff(new Date(), endDate)
            isOut = !dateDiff.done
            if (isOut) {
              resolve({
                status: 'timeout'
              })
            } else {
              this.payCountdown = `${dateDiff.mm}分${dateDiff.ss}秒`
              this.downTime = setTimeout(() => {
                if (this.visible) {
                  updateDowntime()
                }
              }, 1000)
            }
          }
        }
        updateDowntime()
      })
      return Promise.race([
        outPromise,
        new Promise((resolve, reject) => {
          const queryResult = () => {
            if (this.visible) {
              if (isOut) {
                resolve({
                  status: 'timeout'
                })
              } else {
                setTimeout(() => {
                  if (this.visible) {
                    XEAjax.get(`/api/auth/pay/alipay/query/${orderNo}`).then(data => {
                      const rest = this.$utils.getRest(data)
                      if (rest.result.status === 'success') {
                        resolve(rest.result)
                      } else if (rest.result.status === 'wait') {
                        queryResult()
                      } else {
                        resolve(rest.result)
                      }
                    }).catch(() => {
                      const rest = {
                        status: 'error'
                      }
                      reject(rest)
                    })
                  } else {
                    resolve({
                      status: 'cancel'
                    })
                  }
                }, 5000)
              }
            } else {
              resolve({
                status: 'cancel'
              })
            }
          }
          queryResult(orderNo)
        })
      ])
    }
  }
}
</script>

<style scoped lang="scss">
.pay-popup {
  padding: 20px 10px 20px 10px;
  .pay-name,
  .pay-no,
  .pay-type {
    line-height: 26px;
    padding-left: 15px;
  }
  .pay-desc {
    color: #409eff;
    text-align: center;
    padding: 5px 0;
    font-size: 18px;
    user-select: none;
    .pay-icon {
      width: 60px;
      margin-right: 10px;
    }
  }
  .pay-qr {
    display: flex;
    flex-direction: row;
    user-select: none;
    .pay-price {
      text-align: center;
      color: #ff6700;
      .price-unit {
        font-size: 32px;
      }
    }
    .pay-left {
      & > img {
        width: 200px;
        height: 200px;
      }
    }
    .pay-right {
      flex-grow: 1;
      padding-top: 15px;
    }
    .pay-msg {
      font-size: 14px;
      color: #000;
    }
    .pay-down {
      font-size: 40px;
      text-align: center;
      color: red;
    }
    .pay-oper {
      margin-top: 10px;
      text-align: center;
    }
  }
}
.pro-qr {
  text-align: center;
  margin-bottom: 15px;
}
.pro-oper {
  text-align: center;
  margin-top: 15px;
}
.pro-amoun {
  text-align: center;
  margin-top: 15px;
}
.pro-desc {
  padding: 5px 0;
  text-align: center;
}
</style>
