<template>
  <div class="auth-agreement">
    <vxe-modal vxe-modal v-model="visible" title="插件协议" width="800" resize show-zoom esc-closable>
      <div class="at-modal">
        <h4 class="at-title" v-if="selectComp">协议事项</h4>
        <ul class="at-lic">
          <li>企业授权：可用于企业内部、商业项目中</li>
          <li>个人授权：可用于个人商业项目中</li>
          <li>不限制项目数量及多部署</li>
          <li>该授权暂不支持开发票</li>
          <template v-if="code === 'PRO'">
            <li>限制 1 人加入QQ支持群（提供 1 年技术支持）</li>
            <li>授权成功后，您将会收到商用授权协议书.pdf</li>
          </template>
          <li>禁止用于开源项目</li>
          <li>禁止分享插件代码</li>
          <li>禁止将代码进行二次销售</li>
        </ul>
        <h4 class="at-title" v-if="selectComp">常见问题</h4>
        <div class="at-issue">
          <div>插件如何使用，是否影响之前的用法</div>
          <ul>
            <li>如果是按照官网文档的用法，98% 完全兼容，原先项目代码无需任何改动，只需额外引入插件即可</li>
          </ul>
          <div>对应 vxe-table 版本要求</div>
          <ul>
            <li>最低版本支持：v2（2.10+）、v3（3.1+）、v4（4.0+）</li>
          </ul>
          <div>都支持哪些浏览器</div>
          <ul>
            <li>支持主流的现代浏览器，例如 Chrome、Firefox、Safari、Edge、搜狗、QQ、360 等浏览器。 v2 最低兼容 IE11，v3、v4 不支持 IE</li>
          </ul>
          <div>是否有 Bug</div>
          <ul>
            <li>任何项目都不能能保证没有 BUG，但我们会及时修复</li>
          </ul>
        </div>
        <div class="at-contact">如有疑问可以发送邮件到 <a href="mailto:xu_liangzhan@163.com" class="link">xu_liangzhan@163.com</a> 咨询</div>
      </div>
    </vxe-modal>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  name: 'AuthAgreement',
  props: {
    code: String
  },
  data () {
    return {
      visible: false
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList'
    ]),
    selectComp () {
      return this.appList.find(item => item.code === this.code)
    }
  },
  methods: {
    open () {
      this.visible = true
    }
  }
}
</script>

<style lang="scss" scoped>
.at-modal {
  padding: 0 10px 10px 10px;
}
.at-issue {
  padding-left: 15px;
  ul {
    margin: 5px 0;
  }
}
.at-contact {
  margin-top: 15px;
  text-align: right;
}
</style>
