<template>
  <div class="support-page">
    <h2 class="support-title">付费技术支持群<span class="price-unit">￥{{ supportPrice }}</span><span style="font-size: 12px;font-weight: 400;">（快速解决使用过程中遇到的问题）</span></h2>
    <div class="support-group">
      <div class="qq-img">
        <template v-if="disabledSupport">
          <div title="当前不在支持的时间之内！请在工作日 9:30 ~ 18:00 时间内申请！">
            <img src="" style="width: 189px; height: 183px">
          </div>
        </template>
        <template v-else>
          <img v-if="useIssue" src="../assets/image/support/qq.png" style="width: 189px; height: 183px">
          <img v-else src="" style="width: 189px; height: 183px">
        </template>
      </div>
      <div class="qq-step">
        <ul class="step-wrapper">
          <li style="font-size: 15px;color: red;font-weight: 700;" title="如果不会创建在线链接描述问题，建议不要加群！">
            <div>
              <label>
                <input v-model="useIssue" type="radio" name="useIssue" value="">（我不会使用 “<a class="link" href="/vxe-table/issues" target="_blank">在线链接演示问题</a>”）
              </label>
            </div>
            <div>
              <label>
                <input v-model="useIssue" type="radio" name="useIssue" value="1">（我会使用 “<a class="link" href="/vxe-table/issues" target="_blank">在线链接演示问题</a>”）
              </label>
            </div>
          </li>
          <template v-if="!useIssue">
            <li>抱歉！不会在线链接演示问题，无法加群！</li>
          </template>
          <template v-if="disabledSupport">
            <li v-if="useIssue" title="当前不在支持的时间之内！请在工作日 9:30 ~ 18:00 时间内申请！">当前不在支持的时间之内，无法加入！</li>
          </template>
          <template v-else>
            <template v-if="useIssue">
              <li>
                左侧扫码申请加入群或点击<a target="_blank" href="https://qm.qq.com/cgi-bin/qm/qr?k=K0y8dWpsIwFtcpKfb4LlVx44bujw1FaL&jump_from=webapi"><img border="0" src="//pub.idqqimg.com/wpa/images/group.png" alt="vxe-table 技术支持群" title="vxe-table 技术支持群"></a>
              </li>
              <li>点击 <router-link class="link pay-link" :to="{name: 'Pay'}">扫码付款</router-link>，付款后点击“联系收款方”，留言QQ号即可</li>
            </template>
          </template>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      useIssue: '',
      supportPrice: 288
    }
  },
  computed: {
    disabledSupport () {
      const currDate = new Date()
      const day = currDate.getDay()
      const hours = currDate.getHours()
      if (day === 0 || !((hours >= 9 && hours <= 11) || (hours >= 14 && hours <= 17) || (hours >= 19 && hours <= 21))) {
        return true
      }
      if (currDate > new Date(2021, 1, 10)) {
        return true
      }
      return false
    }
  }
}
</script>

<style lang="scss" scoped>
.support-title {
  text-align: center;
}
.price-unit {
  font-size: 18px;
  color: #ff6700;
  padding: 0 5px;
  font-weight: 700;
}
.support-group {
  margin: 15px auto;
  width: 520px;
  display: flex;
  flex-direction: row;
  .qq-img {
    flex-shrink: 0;
  }
  .qq-step {
    flex-grow: 1;
  }
  .step-wrapper {
    & > li {
      list-style-type: circle;
      line-height: 32px;
    }
  }
  .pay-link {
    color: #22AB38;
  }
}
.is-disabled {
  cursor: no-drop;
}
.support-pay {
  margin-top: 50px;
  text-align: center;
}
</style>
