<template>
  <div v-if="selectComp" class="page-view">
    <div class="comp-tip">
      <div>
        <span class="comp-name">{{ selectComp.name }}</span>
        <template v-if="authComp">
          <span class="comp-type"> - 已授权{{ $utils.getCompType(authComp.authType) }}</span>
        </template>
        <template v-else>
          <span class="comp-type"> - {{ $utils.getCompType(selectComp.codeType) }}</span>
          <span class="comp-amoun">￥{{ selectComp.amount }}</span>
          <span class="comp-expire">/ {{ $utils.getCompExpiration(selectComp.deadline) }}</span>
        </template>
      </div>
      <ul>
        <li>{{ selectComp.codeType === 'src' ? '该授权包含完整源码' : '该授权不提供源码'}}</li>
      </ul>
      <div class="comp-agreement">
        <a class="link" @click="$refs.authAgreement.open()">查看详细协议</a>
      </div>
      <div class="comp-oper">
        <vxe-button class="btn" status="danger" @click="$refs.compOrder.open()" v-if="!authComp">购买授权</vxe-button>
        <vxe-button class="btn" status="primary" @click="$refs.releaseLog.open()">{{ authComp ? '所有版本' : '更新日志' }}</vxe-button>
        <vxe-button class="btn" @click="$router.replace({name: backName})">返回</vxe-button>
      </div>
    </div>
    <div class="comp-preview">
      <div class="preview">
        <img :src="imgUrl">
      </div>
      <ul class="features">
        <li v-for="(item, index) in selectComp.features" :key="index">{{ item }}</li>
      </ul>
    </div>
    <div class="comp-desc">
      <pre>
        <pre-code class="text">{{ apiDesc[0] }}</pre-code>
      </pre>
    </div>
    <pre>
      <pre-code class="html">{{ demoCodes[0] }}</pre-code>
      <pre-code class="javascript">{{ demoCodes[1] }}</pre-code>
    </pre>
    <release-log ref="releaseLog" :code="code"></release-log>
    <comp-order ref="compOrder" :code="code"></comp-order>
    <auth-agreement ref="authAgreement" :code="code"></auth-agreement>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {
      code: 'FilterComplexInput',
      backName: 'Filter',
      imgUrl: require('../../assets/image/plugin/FilterComplexInput.gif'),
      apiDesc: [
        `
        购买授权后可以自行根据项目需求去定制修改，默认用于本地筛选

        安装：
        将 plugins/table/renderer/filter 复制到项目任意目录中，引用即可

        参数：
        filters=[{data: {sType: 'include', sVal: '', isSensitive: false}}]"
        
        说明：
        sType: 筛选类型（incl包含、eq等于、gt大于、lt小于）
        sVal：搜索的值
        isSensitive：是否区分大小写
        
        参数：
        filter-render={ name: 'FilterComplexInput', cellType: 'string', props: {} }
        
        说明：
        cellType: 单元格的类型（string字符串、number数值）
        props：对应 vxe-input 配置项
        `
      ],
      demoCodes: [
        `
        <vxe-table
          border
          show-overflow
          height="500"
          :data="tableData">
          <vxe-table-column type="seq" width="60"></vxe-table-column>
          <vxe-table-column field="a" title="A1" :filters="[{ data: { isSensitive: false, sVal: '' } }]" :filter-render="{ name: 'FilterSimpleInput', cellType: 'number' }"></vxe-table-column>
          <vxe-table-column field="b" title="B1" :filters="[{ data: { isSensitive: false, sVal: '' } }]" :filter-render="{ name: 'FilterSimpleInput' }"></vxe-table-column>
          <vxe-table-column field="c" title="C1" :filters="[{ data: { sType: 'include', isSensitive: false, sVal: '' } }]" :filter-render="{ name: 'FilterComplexInput', cellType: 'number' }"></vxe-table-column>
          <vxe-table-column field="d" title="D1" :filters="[{ data: { sType: 'include', isSensitive: false, sVal: '' } }]" :filter-render="{ name: 'FilterComplexInput' }"></vxe-table-column>
        </vxe-table>
        `,
        `
        export default {
          data () {
            return {
              tableData: [
                { id: 10001, a: '', b: '', c: '', d: '', e: '', f: '', g: '' },
                { id: 10002, a: '', b: '', c: '', d: '', e: '', f: '', g: '' },
                { id: 10003, a: '', b: '', c: '', d: '', e: '', f: '', g: '' },
                { id: 10004, a: '', b: '', c: '', d: '', e: '', f: '', g: '' }
              ]
            }
          }
        }
        `
      ]
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList'
    ]),
    authComp () {
      if (this.loginToken && this.loginInfo && this.loginInfo.compList) {
        return this.loginInfo.compList.find(comp => comp.appCode === this.code)
      }
      return null
    },
    selectComp () {
      return this.appList.find(item => item.code === this.code)
    }
  }
}
</script>
