import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

const store = new Vuex.Store({
  state: {
    loginStatus: 0,
    loginToken: sessionStorage.getItem('AUTH_TOKEN'),
    loginInfo: null,
    appList: []
  },
  mutations: {
    updateLoginInfo (state) {
      state.loginStatus++
    },
    setLoginToken (state, token) {
      state.loginToken = token
    },
    setLoginInfo (state, obj) {
      state.loginInfo = obj
    },
    setAppList (state, list) {
      state.appList = list
    }
  },
  actions: {
  },
  modules: {
  }
})

export default store
