<template>
  <div class="filter-page">
    <div class="filter-preview">
      <div class="preview-item" v-for="item in filterRenderList" :key="item.code">
        <div class="preview-contnet">
          <div class="preview-title">
            <span class="comp-name">{{ item.name }}</span>
            <template v-if="getCompPrivilege(item)">
              <span class="comp-auth"> - 已授权<span>{{ $utils.getCompType(item.codeType) }}</span></span>
            </template>
            <template v-else>
              <span class="comp-type"> - {{ $utils.getCompType(item.codeType) }}</span>
              <span class="comp-amoun">￥{{ item.amount }}</span>
              <span class="comp-expire">/ {{ $utils.getCompExpiration(item.deadline) }}</span>
            </template>
            <vxe-button class="btn" status="primary" size="mini" @click="$router.replace({name: item.code})" style="margin-left: 15px;">查看文档</vxe-button>
          </div>
          <img class="preview-img" :src="imgMaps[item.code]">
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {
      imgMaps: {
        FilterComplexInput: require('../assets/image/plugin/FilterComplexInput.gif'),
        FilterCombination: require('../assets/image/plugin/FilterCombination.gif')
      }
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList'
    ]),
    filterRenderList () {
      return this.appList.filter(item => item.group === 'filters')
    }
  },
  destroyed () {
    clearTimeout(this.downTime)
  },
  methods: {
    getCompPrivilege (item) {
      return this.loginToken && this.loginInfo && this.loginInfo.compList && this.loginInfo.compList.find(comp => comp.appCode === item.code)
    }
  }
}
</script>

<style lang="scss" scoped>
.help-icon {
  display: inline-block;
  width: 18px;
  height: 18px;
  font-size: 12px;
  border-radius: 50%;
  margin: 0 5px;
  color: #fff;
  background-color: #000;
  text-align: center;
  cursor: help;
}
.filter-preview {
  width: 1200px;
  margin: 0 auto;
  &:after {
    content: "";
    display: block;
    clear: both;
    height: 0;
    overflow: hidden;
    visibility: hidden;
  }
  .preview-item {
    width: 600px;
    height: 420px;
    float: left;
    width: 50%;
    padding: 10px;
  }
  .preview-contnet {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 4px;
    border: 1px solid #EBEEF5;
    box-shadow: 1px 2px 10px #eee;
    background-color: #fff;
  }
  .preview-title {
    margin: 0;
    padding-top: 15px;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .preview-img {
    width: 100%;
  }
}
</style>
