<template>
  <div class="pro-page">
    <div class="pro-desc" v-if="selectComp">
      <div class="pro-price">
        <span class="comp-name">{{ selectComp.name }}</span>
        <template v-if="authComp">
          <span class="comp-auth" > - 已授权<span>{{ $utils.getCompType(authComp.authType) }}</span></span>
        </template>
        <template v-else>
          <span class="comp-type"> - {{ $utils.getCompType(selectComp.codeType) }}</span>
          <span class="comp-amoun">￥{{ selectComp.amount }}</span>
          <span class="comp-expire">/ {{ $utils.getCompExpiration(selectComp.deadline) }}</span>
        </template>
        <vxe-button class="btn" status="primary" size="mini" @click="$router.replace({name: 'ProExtension'})" style="margin-left: 15px;">查看文档</vxe-button>
      </div>
    </div>
    <div class="pro-preview">
      <div class="preview-item" v-for="item in proList" :key="item.code">
        <div class="preview-contnet">
          <h3 class="preview-title">{{ item.title }}</h3>
          <img class="preview-img" :src="item.previewUrl">
        </div>
      </div>
    </div>
    <release-log ref="releaseLog" :code="code"></release-log>
  </div>
</template>

<script>
import { mapState } from 'vuex'

export default {
  data () {
    return {
      code: 'PRO',
      version: '1.0.12',
      proPrice: 1200,
      proList: [
        {
          title: '鼠标区域选取',
          code: 'proArea',
          previewUrl: require('../assets/image/plugin/proArea.gif')
        },
        {
          title: '鼠标多重区域选取、延伸扩大区域',
          code: 'proMultiArea',
          previewUrl: require('../assets/image/plugin/proMultiArea.gif')
        },
        {
          title: '固定列区域选取',
          code: 'proFixedAera',
          previewUrl: require('../assets/image/plugin/proFixedAera.gif')
        },
        {
          title: '复制、剪贴、粘贴',
          code: 'proClip',
          previewUrl: require('../assets/image/plugin/proClip.gif')
        },
        {
          title: '合并、取消合并',
          code: 'proMerge',
          previewUrl: require('../assets/image/plugin/proMerge.gif')
        },
        {
          title: '查找和替换',
          code: 'proFNR',
          previewUrl: require('../assets/image/plugin/proFNR.gif')
        },
        {
          title: '常用的快捷键',
          code: 'proKeyboard',
          previewUrl: require('../assets/image/plugin/proKeyboard.gif')
        },
        {
          title: '右键快捷菜单',
          code: 'proContextmenu',
          previewUrl: require('../assets/image/plugin/proContextmenu.gif')
        },
        {
          title: '集成 echarts 图表渲染',
          code: 'proCharts',
          previewUrl: require('../assets/image/plugin/proCharts.gif')
        },
        {
          title: '同时支持区域选取、虚拟滚动',
          code: 'proVirtualScroll',
          previewUrl: require('../assets/image/plugin/proVirtualScroll.gif')
        },
        {
          title: '同时支持区域选取、虚拟滚动、虚拟合并',
          code: 'proVirtualMergeScroll',
          previewUrl: require('../assets/image/plugin/proVirtualMergeScroll.gif')
        }
      ]
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo',
      'appList'
    ]),
    authComp () {
      if (this.loginToken && this.loginInfo && this.loginInfo.compList) {
        return this.loginInfo.compList.find(comp => comp.appCode === this.code)
      }
      return null
    },
    selectComp () {
      return this.appList.find(item => item.code === this.code)
    }
  }
}
</script>

<style lang="scss" scoped>
.price-unit {
  font-size: 18px;
  color: #ff6700;
  padding: 0 5px;
  font-weight: 700;
}
.pro-desc {
  position: relative;
  width: 1200px;
  margin: 0 auto;
  padding: 0 10px;
}
.pro-price {
  padding: 15px;
  border-radius: 4px;
  border: 1px solid #EBEEF5;
  box-shadow: 1px 2px 10px #eee;
  background-color: #fff;
}
.comp-auth {
  font-size: 13px;
}
.help-icon {
  display: inline-block;
  width: 18px;
  height: 18px;
  font-size: 12px;
  border-radius: 50%;
  margin: 0 5px;
  color: #fff;
  background-color: #000;
  text-align: center;
  cursor: help;
}
.pro-preview {
  width: 1200px;
  margin: 0 auto;
  &:after {
    content: "";
    display: block;
    clear: both;
    height: 0;
    overflow: hidden;
    visibility: hidden;
  }
  .preview-item {
    width: 600px;
    height: 420px;
    float: left;
    width: 50%;
    padding: 10px;
  }
  .preview-contnet {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 4px;
    border: 1px solid #EBEEF5;
    box-shadow: 1px 2px 10px #eee;
    background-color: #fff;
  }
  .preview-title {
    margin: 0;
    padding-top: 15px;
    text-align: center;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .preview-img {
    width: 100%;
  }
}
</style>
